import { makeObservable, observable, action } from 'mobx';
import axios from 'axios';
import servicesDataFromMyJson from "../Services.json"
import workedDaysDataFromMyJson from "../Time (2).json"

class FetchStore {
    // servicesData = {};
    servicesData = servicesDataFromMyJson;

    // workedDaysData = {};
    workedDaysData = workedDaysDataFromMyJson;

    constructor() {
        makeObservable(this, {
            servicesData: observable,
            workedDaysData: observable,
            fetchServicesData: action,
            fetchWorkedDaysData: action,
        }, { deep: true, });
    }

    setServicesData = (newData) => {
        this.servicesData = newData;
    }

    fetchServicesData = async () => {
        try {
            const response = await axios.get('http://192.168.31.223:8080/users/BigBallsSoftwareBot/services');
            this.servicesData = (response.data);
            console.log("Загружены данны о сервисах");
        } catch (error) {
            console.error('Ошибка загрузки данных о сервисах', error);
        }
    }

    fetchWorkedDaysData = async () => {
        try {
            const response = await axios.get('http://192.168.31.223:8080/users/BigBallsSoftwareBot/schedule');
            this.workedDaysData = (response.data);
            console.log("Загружены данные о рабочих днях");
        } catch (error) {
            console.error('Ошибка загрузки данных о рабочих днях', error);
        }
    }

}

const fetchStore = new FetchStore();

export default fetchStore;
