import React, { useEffect } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import "./App.css";
import Services from "./pages/ServicesPage";
import Info from "./pages/info/Info";
import SelectTime from "./pages/SelectTime";
import LastStep from "./pages/lastStep/LastStep";
import fetchStore from "./store/FetchStore";

function App() {
  useEffect(() => {
    fetchStore.fetchServicesData(); // Правильное место для вызова метода

    fetchStore.fetchWorkedDaysData();
    document.title = " Title";
  }, []);

  return (
    <BrowserRouter>
      <Routes>
        <Route path="*" element={<Services />} />
        <Route path="/info/:id" element={<Info />} />
        <Route path="/selectTime" element={<SelectTime />} />
        <Route path="/lastStep" element={<LastStep />} />
      </Routes>
    </BrowserRouter>
  );
}

export default App;