import { useState, useEffect } from "react";
import cl from "./DualInputDropdown.module.css"
import Select from "react-select";
import { InputMask } from '@react-input/mask';

const options = [
    { value: "0", label: "Не напоминать" },
    { value: "15", label: "Напомнить за 15 минут" },
    { value: "30", label: "Напомнить за 30 минут" },
    { value: "1", label: "Напомнить за 1 час" },
    { value: "2", label: "Напомнить за 2 часа" },
    { value: "6", label: "Напомнить за 6 часов" },
    { value: "24", label: "Напомнить за 1 сутки" },
];

const isMobileDevice = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

const customComponents = {
    IndicatorSeparator: () => null
};

const customStyles = {
    option: (provided, state) => {

        const isFirstOption = state.options[0] === state.data;
        const isLastOption = state.options[state.options.length - 1] === state.data;

        return {
            ...provided,
            display: 'flex',
            alignItems: 'center',
            color: state.isSelected ? "var(--tg-theme-link-color)" : "var(--tg-theme-text-color)",
            backgroundColor: 'var(--tg-theme-bg-color)',
            ':active': {
                ...provided[':active'],
                backgroundColor: '#E5F0FF',
            },
            //   padding: "0",
            paddingLeft: "16px",
            fontSize: isMobileDevice ? "17px" : "15px",
            lineHeight: "22px",
            letterSpacing: "-0.43px",
            fontFamily: "'SF Pro Regular'",
            height: isMobileDevice ? "44px" : "33px",
            borderBottom: isLastOption ? 'none' : '1px solid var(--tg-theme-secondary-bg-color)',
            borderBottomLeftRadius: isLastOption ? "10px" : "0",
            borderBottomRightRadius: isLastOption ? "10px" : "0",
            borderTopLeftRadius: isFirstOption ? "10px" : "0",
            borderTopRightRadius: isFirstOption ? "10px" : "0",
        };
    },

    control: (defaultStyles) => ({
        ...defaultStyles,
        backgroundColor: 'var(--tg-theme-bg-color)',

        alignItems: "center",
        height: '44px',


        borderRadius: "0",
        borderBottomRightRadius: '10px',
        borderBottomLeftRadius: '10px',
        border: "none",
        boxShadow: "none",
        fontSize: "17px",
        lineHeight: "22px",
        letterSpacing: "-0.43px",
        fontFamily: "'SF Pro Regular'",
        paddingLeft: "6px",
        paddingRight: "12px",

    }),

    menuList: (provided) => ({
        ...provided,
        padding: 0,
        marginBottom: isMobileDevice ? '8px' : '0px',
        borderRadius: '10px',
        overflowY: "none"
    }),

    singleValue: (defaultStyles) => ({
        ...defaultStyles,
        color: "var(--tg-theme-text-color)",
    }),

    menu: (provided) => ({
        ...provided,
        borderRadius: '10px',
        backgroundColor: 'var(--tg-theme-bg-color)',
    }),
};

const DualInputDropdown = () => {
    const [nameOfClient, setNameOfClient] = useState('');
    const [phoneNumber, setPhoneNumber] = useState('');

    const handlePhoneNumberChange = (e) => {
        setPhoneNumber(e.target.value);
    };

    const handlePhoneNumberFocus = (e) => {
        if (e.target.value === '') {
            e.target.value = '+7 (';
            setPhoneNumber('+7 ('); // Установите начальное значение при фокусировке
        }
    };

    const [selectedOption, setSelectedOption] = useState(() => {
        const savedOption = localStorage.getItem('selectedOption');
        return savedOption ? JSON.parse(savedOption) : { value: "0", label: "Не напоминать" };
    });


    const tg = window.Telegram.WebApp;

    useEffect(() => {
        const isPhoneNumberComplete = phoneNumber.length === 18;

        if (nameOfClient.trim() && isPhoneNumberComplete ) {            
            tg.MainButton.color = "#34C759";
            tg.MainButton.textColor = "#FFFFFF";
            tg.MainButton.enable();
        } else {
            tg.MainButton.disable();
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [nameOfClient, phoneNumber]);

    useEffect(() => {
        const savedName = localStorage.getItem('nameOfClient');
        const savedPhoneNumber = localStorage.getItem('phoneNumber');
        
        if (savedName) {
            setNameOfClient(savedName);
        }
        if (savedPhoneNumber) {
            setPhoneNumber(savedPhoneNumber);
        }
    }, []);

    useEffect(() => {
        localStorage.setItem('nameOfClient', nameOfClient);        
    }, [nameOfClient]);

    useEffect(() => {
        localStorage.setItem('phoneNumber', phoneNumber);
    }, [phoneNumber]);

    useEffect(() => {
        localStorage.setItem('selectedOption', JSON.stringify(selectedOption));
    }, [selectedOption]);



    return (
        <div className={cl.container}>
            <div className={cl.inputWrapper} style={{ borderTopLeftRadius: '10px', borderTopRightRadius: '10px' }}>
                <input
                    type="text"
                    value={nameOfClient}
                    onChange={(e) => setNameOfClient(e.target.value)}
                    placeholder="Имя"
                    className={cl.inpt}
                    style={{ borderTopLeftRadius: '10px', borderTopRightRadius: '10px' }}
                />
                <div className={cl.borderBottom}></div>
            </div>
            <div className={cl.inputWrapper}>
                <InputMask
                    mask="+7 (___) ___-__-__"
                    value={phoneNumber}
                    replacement={{ _: /\d/ }}
                    onChange={handlePhoneNumberChange}
                    onFocus={handlePhoneNumberFocus}
                    className={cl.inpt}
                    placeholder="Номер телефона"
                />
                <div className={cl.borderBottom}></div>
            </div>

            <Select
                components={customComponents}
                isSearchable={false}
                defaultValue={selectedOption}
                onChange={setSelectedOption}
                options={options}
                styles={customStyles}
            />
        </div>
    )
}

export default DualInputDropdown
