import React from "react";
import { HeadlineBody } from "../../textStyles/TextStyleComponents";
import ServicesHeader from "../../components/header/ServicesHeader";
import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import cl from "./Info.module.css"
import Photos from "../../components/info/Photos";
import { observer } from "mobx-react-lite";
import { useCheckboxStore } from "../../store/CheckboxStore";
import fetchStore from "../../store/FetchStore";


const Info = observer(() => {



  // const [currentIndex, setCurrentIndex] = useState(0);

  // const showNextImage = () => {
  //   const nextIndex = (currentIndex + 1) % images.length;
  //   setCurrentIndex(nextIndex);
  // };


  let tg = window.Telegram.WebApp;
  const navigate = useNavigate();
  const { id } = useParams();
  const checkboxStore = useCheckboxStore();

  // const [itemId, setItemId] = useState('');
  const [itemInfo, setItemInfo] = useState({
    name: '',
    photos: [],
    time: '',
    info: ''
  });


  useEffect(() => {
    tg.BackButton.show();
    tg.BackButton.onClick(() => {
      navigate("*");
      if (checkboxStore.getSelectedCheckboxCount() !== 0) {
        tg.MainButton.show();
      }
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const getItemById = () => {
      let selectedItem = { name: '', photos: [], time: '', description: '' };

      // Итерация по объекту с помощью Object.values и поиск в каждой категории
      Object.values(fetchStore.servicesData).forEach(categoryItems => {
        const foundItem = categoryItems.find(item => String(item.id) === id);
        if (foundItem) {
          selectedItem = {
            name: foundItem.name,
            // Обновите поле img, если оно доступно в новом JSON
            photos: Array.isArray(foundItem.photos) ? foundItem.photos : [],
            time: foundItem.duration,
            description: foundItem.description
          };
        }
      });
      return selectedItem;
    };
    setItemInfo(getItemById());
  }, [id]);


  const moment = require('moment');

  function formatMinutesToTimeString(minutes) {
    const duration = moment.duration(minutes, 'minutes');
    const hours = duration.hours();
    const mins = duration.minutes();

    let result = '';
    if (hours > 0) {
      result += hours + ' час';
      if (hours > 1) result += 'а';
      result += ' ';
    }
    if (mins > 0) {
      result += mins + ' минут';
    }
    return result;
  }

  return (
    <div>
      {itemInfo.photos.length > 0 && (
        <Photos photos={itemInfo.photos} />
      )}

      <ServicesHeader>{itemInfo.name}</ServicesHeader>

      <HeadlineBody className={cl.time}>Длительность: {formatMinutesToTimeString(itemInfo.time)} </HeadlineBody>

      <HeadlineBody className={cl.description}>{itemInfo.description}</HeadlineBody>
    </div>
  )
})

export default Info;