import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import cl from "./Photos.module.css";
import { Pagination, Navigation } from 'swiper/modules';
import { useState } from "react";

import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';


import photo1 from "../../photo/strijka_1.jpeg"
import photo2 from "../../photo/strijka_2.jpeg"
import photo3 from "../../photo/strijka_3.jpeg"
import photo4 from "../../photo/strijka_4.jpeg"
import photo5 from "../../photo/strijka_5.jpeg"
import photo6 from "../../photo/strijka_6.jpeg"

const Photos = ({ photos }) => {

    const [expandedImage, setExpandedImage] = useState(null);

    const isMobileDevice = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);

    const handleImageClick = (imageSrc) => {

        setExpandedImage(imageSrc);
    };

    const handleCloseExpandedImage = () => {
        setExpandedImage(null);
    };

    

    const base64ToImageUrl = (base64) => `data:image/jpeg;base64,${base64}`;

    return (
        <div className={cl.containerSwiper}>
            <Swiper
                slidesPerView={1}
                spaceBetween={30}
                loop={true}
                pagination={{
                    clickable: true,
                }}
                navigation={true}
                modules={[Pagination, Navigation]}
                className={cl.mySwiper}
            >
                {photos.map((photoBase64, index) => (
                    <SwiperSlide key={index} className={cl.swiperSlide}>
                        <img src={base64ToImageUrl(photoBase64)} alt={`Фото ${index}`} className={cl.swiperSlideImg} />
                    </SwiperSlide>
                ))}
            </Swiper>
        </div>
    )
}

export default Photos